.edit-container {

    .edit {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        width: 500px;
        height: fit-content;
        background-color: $clr2;
        padding: 20px 30px;
        border-radius: 20px;
        display: flex;
        flex-direction: column;
        box-shadow: 0 2px 9px 0 rgb(0 0 0 / 10%);
        z-index: 5;
    }

    input,
    textarea,
    select {
        padding: 3px 5px;
        border: 2px solid $clr3;
        border-radius: 8px;
        width: 185px;
    }

    textarea {
        height: 130px;
    }

    .actions {
        display: flex;
        justify-content: space-between;
        width: 185px;
        align-self: flex-end;
    }

    .save {
        background-color: $clr3;
        padding: 8px 9px;
        color: $clr5;
        border: 1px solid $clr2;
        border-radius: 6px;
        transition: all .3s;

        &:hover {
            background-color: darken($clr3, 10%);
        }
    }

    .cancel {
        background-color: $clr1;
        padding: 5px 9px;
        color: $clr5;
        border: 1px solid $clr2;
        border-radius: 6px;
        transition: all .3s;

        &:hover {
            background-color: darken($clr2, 20%);
        }
    }

    .toggle-menu-btn {
        display: none;

        @media (max-width: $mobile-size) {
            display: flex;
            background-color: $clr2;
            border: none;
        }
    }

    .toggle-menu-btn.hide-btn {
        opacity: 0;
        visibility: hidden;
    }

    .toggle-menu-screen {
        display: block;
        position: fixed;
        width: 100vw;
        height: 100vh;
        left: 0;
        top: 0;
        background: rgba(42, 42, 42, 0.6);
        z-index: 4;
        opacity: 0.5;
        // visibility: hidden;
        cursor: pointer;
        transition: opacity .5s;
    }

    .toggle-menu-screen.close-nav {
        visibility: visible;
        opacity: 1;
    }
}